.main-color-item {
  position: relative;
  height: 44px;
  margin-right: 4px;
  padding: 0 12px;
  font-size: 14px;
  font-family: Consolas, sans-serif;
  line-height: 44px;
  cursor: pointer;
  transition: all 0.2s;
}

.main-color-item:first-child {
  border-radius: 4px 4px 0 0;
}

.main-color-item:last-child {
  border-radius: 0 0 4px 4px;
}

.main-color-item:hover {
  margin-right: -8px;
  border-radius: 0 4px 4px 0;
}

.main-color-item .main-color-text {
  float: left;
  transition: all 0.3s;
}

.main-color-item .main-color-value {
  position: relative;
  left: 3px;
  float: right;
  transform: scale(0.85);
  transform-origin: 100% 50%;
  opacity: 0;
  transition: all 0.3s;
}

.color-title {
  margin: 0 0 24px;
  color: #5c6b77;
  font-weight: 500;
  font-size: 22px;
  text-align: center;
  text-transform: capitalize;
}

.color-description {
  display: block;
  color: #777;
  font-weight: lighter;
  font-size: 14px;
}

.main-color:hover .main-color-value {
  left: 0;
  opacity: 0.7;
}

.color-palette-horizontal {
  width: 100%;
}

.color-palette-horizontal-dark {
  height: 303px;
  padding: 32px 28px;
  background-color: #141414;
}

.color-palette-horizontal-dark .color-palette-picker {
  margin-bottom: 0;
}

.color-palette-horizontal-dark .color-palette-pick {
  color: #ffffffa6;
  text-align: left;
}

.color-palette-horizontal-dark .color-palette-pick-hex {
  color: #ffffffa6;
}

.ant-row-rtl .color-palette-horizontal-dark .color-palette-pick {
  direction: rtl;
  text-align: right;
}

.color-palette-horizontal .main-color {
  display: flex;
}

.color-palette-horizontal .main-color-item {
  position: relative;
  flex: 1;
  height: 86px;
  margin-right: 0;
  padding: 37px 0 0;
  line-height: normal;
  text-align: center;
  border-radius: 0;
}

.color-palette-horizontal .main-color-item .main-color-text {
  float: none;
}

.color-palette-horizontal .main-color-item:hover {
  height: 96px;
  margin-top: -10px;
  border-radius: 4px 4px 0 0;
}

.color-palette-horizontal .main-color-value {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  text-align: center;
  transform-origin: unset;
}

.color-palette-horizontal .main-color:hover .main-color-item {
  padding-top: 8px;
}

.color-palette-horizontal .main-color:hover .main-color-value {
  bottom: 8px;
  opacity: 0.7;
